import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Agreement from '../views/Agreement.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/home/:lang',
    name: 'Home',
    component: Home
  },
  {
    path: '/agreement/:lang',
    name: 'Agreement',
    component: Agreement
  }
]

const router = new VueRouter({
  routes
})

export default router
